import React, {Component} from 'react';

class Error extends Component {
    render() {

        let errorString = "";
        if(this.props.errorCode.indexOf("userNotFound") > -1)
            errorString = 'No Github user found with username "' + this.props.errorCode.substring(13,this.props.errorCode.length - 5) + '"';
        if(this.props.errorCode === "noUserEntered")
            errorString = "Enter a username to search for first!";
        return (
            <div hidden = {this.props.hidden}>
                <div className="alert alert-danger text-center" style={{width: "25rem", marginBottom: "1rem", margin: "auto"}}>
                        {errorString}
                </div>
            </div>
        );
    }
}

export default Error;


