import React, {Component} from 'react';

class Overview extends Component {
    render() {
        
        if(this.props.userData.bio === null)
            this.props.userData.bio = "This user does not have a bio.";

        return (
            <div className="card text-center dark border-warning" style={{width: "95%", margin: "auto", marginTop: "15px"}}>
                <h5 className="card-header border-warning font-weight-bold bg-warning">
                    {"@" + this.props.userData.login}
                </h5>
                <div className="card-body row">
                    <div className="col-md-3">
                        <img className="img-fluid" src={this.props.userData.avatar_url} alt="This user does not have a profile icon"/>
                    </div>

                    <div className="col-md-6">
                        <h5 className="card-title text-white font-weight-bold">{this.props.userData.name}</h5>
                        <p className="card-text text-white">{this.props.userData.bio}</p>
                        <button href={"https://github.com/" + this.props.userData.login} className="btn btn-primary">View on Github</button>
                    </div>

                    <div className="col-md-3" style={{marginTop: "1rem"}}>
                        <a href={"https://github.com/" + this.props.userData.login + "/repositories"} rel="">
                            <div className="card text-white bg-success mb-3" style={{height: "1.5rem"}}>
                                <h6 className="card-text" >{this.props.userData.public_repos} Repos</h6>
                            </div>
                        </a>

                        <a href={"https://github.com/" + this.props.userData.login + "/gists"} rel="">
                            <div className="card text-white bg-info mb-3" style={{height: "1.5rem"}}>
                                <h6 className="card-text" >{this.props.userData.public_gists} Gists</h6>
                            </div>
                        </a>

                        <a href={"https://github.com/" + this.props.userData.login + "/followers"} rel="">
                            <div className="card text-white bg-primary mb-3" style={{height: "1.5rem"}}>
                                <h6 className="card-text" >{this.props.userData.followers} Followers</h6>
                            </div>
                        </a>

                        <a href={"https://github.com/" + this.props.userData.login + "/following"} rel="">
                            <div className="card text-white bg-danger mb-3" style={{height: "1.5rem"}}>
                                <h6 className="card-text" >{this.props.userData.following} Following</h6>
                            </div>
                        </a>

                    </div>
                </div>
            </div>
        );
    }
}

export default Overview;


