import React, {Component} from 'react';
import $ from "jquery";
//import $ from 'jquery';

class Navbar extends Component {

    componentDidMount(){
        let navbar = document.getElementById("navbar");
        let nav = navbar.classList;

        $("#navbar-toggle").click(() => {
            if(nav.contains("show"))
                nav.remove("show");
            else
                nav.add("show");
        });

        $(document).on('submit','#search-nav',() => {
            //console.log("search nav clicked");
            nav.remove("show");
            return false;
        });
    }

    render() {

        return(
            <nav className="navbar navbar-expand-md navbar-dark bg-primary" style={{marginBottom: "1rem"}}hidden = {this.props.hidden}>
                <a className="navbar-brand" style={{color: "white", cursor: "pointer"}} id="home"><i className="icon-github" /> Profile Viewer</a>

                <button className="navbar-toggler" type="button" id="navbar-toggle" hidden={this.props.searchHidden}>
                    <span className="navbar-toggler-icon"/>
                </button>

                <div className="collapse navbar-collapse" id="navbar" hidden={this.props.searchHidden}>
                    <ul className="navbar-nav mr-auto">

                    </ul>

                    <form className="form-inline my-2 my-lg-0" id="search-nav">
                            <input className="form-control mr-sm-2 bg-dark border-primary text-white" style={{width: "18rem"}} type="text" placeholder="Enter Github Username" id="username_nav"/>
                            <button className="btn btn-success my-2 my-sm-0" style={{marginLeft: "1rem", flex: "grow"}}>Go!</button>
                    </form>
                </div>

            </nav>
        );
    }
}


export default Navbar;


