import React, {Component} from 'react';
import Repo from './Repo';

class RepoList extends Component {
    render() {

        //console.log(this.props.repoData);
        let repos = [];

        if(!(this.props.repoData === undefined)) {
            for (let i = 0; i < this.props.repoData.length + (this.props.repoData.length%3) -1 ; i++) {
                repos.push(<Repo repo={this.props.repoData[i]}/>)
            }
        }

        let deck = [];

        for(let k = 0; k < repos.length; k+=3){
            deck.push(<div className="card-deck" style={{width: "95%", margin: "auto", marginTop: "15px", marginBottom: "15px"}} >
                {[repos[k], repos[k+1], repos[k+2]]}
            </div>);
        }



        return (
            <div className="card border-danger dark" style={{width: "95%", margin: "auto", marginTop: "15px"}} hidden={this.props.hidden}>
                <h5 className="card-header bg-danger text-center font-weight-bold">Repositories</h5>
                {deck}
            </div>
        );
    }
}

export default RepoList;


