import React, { Component } from 'react';
import './bootstrap.css';
import './App.css';
import './fontello-GPV/css/gpv.css'
import $ from 'jquery';

//Components
import Navbar from './Navbar.jsx';
import Profile from './Profile.jsx'
import Search from './Search.jsx';
import Error from './Error.jsx';

class App extends Component {
    constructor(props){
        super(props);
        this.state = {
            username: '$user',
            userData: [],
            userRepos: [],
            navHide: false,
            profHide: true,
            errHide: true,
            errCode: "",
            searchHide: false,
            navSearchHide: true,
        };
        this.updateUser = this.updateUser.bind(this);
    }

    getUserData(){
        if(this.state.username === "")
            this.updateStates();
        else {
            //Main Data
            $.ajax({
                url: 'https://api.github.com/users/' + this.state.username + '?client_id=' + this.props.clientId + '&clientSecret=' + this.props.clientSecret + '&access_token=' + this.props.accessToken,
                dataType: 'json',
                cache: false,
                success: (data) => {
                    this.setState(() => {
                            return {userData: data}
                        },
                        () => {
                            this.updateStates();
                        });
                },
                error: (xhr, status, err) => {
                    this.setState(() => {
                            return {username: this.state.username + "$null"}
                        },
                        () => {
                            this.updateStates();
                        });
                }

            });

            $.ajax({
                url: 'https://api.github.com/users/' + this.state.username + '/repos?client_id=' + this.props.clientId + '&clientSecret=' + this.props.clientSecret + '&access_token=' + this.props.accessToken,
                dataType: 'json',
                cache: false,
                success:(data) => {
                    //console.log(data);
                    this.setState({userRepos: data});
                },
                error: (xhr, status, err) => {
                }

            });
        }
    }

    componentDidMount(){
        $(document).on('submit','#search-search',() => {
            this.changeUser("search");
            return false;
        });
        $(document).on('submit','#search-nav',() => {
            this.changeUser("nav");
            return false;
        });
        $("#home").click(() => {
            this.setState({
                username: '',
                userData: [],
                userRepos: [],
                navHide: false,
                profHide: true,
                errHide: true,
                searchHide: false,
                navSearchHide: true
            });
        });

    }

    changeUser(loc){
        const user = document.getElementById("username_" + loc);
        let userChange;
        if(!(user === null)){
            userChange = !(this.state.username === user.value);
            if(userChange)
                this.updateUser(user.value);
            user.value = "";
        }
    }

    updateUser(user){
        this.setState(() => {return {username: user}},
            () => {
                this.getUserData();
            });
    }

    updateStates(){
        this.setState({
            navHide: false,
            navSearchHide: false,
            profHide: false,
            errHide: true,
            searchHide: true,
        });

        if(this.state.username === ''){
            this.setState({
                username: "$user",
                navHide: false,
                navSearchHide: true,
                profHide: true,
                errHide: false,
                errCode: "noUserEntered",
                searchHide: false,
            });
        }

        if(!(this.state.username.indexOf("$null") === -1)){
            this.setState({
                navHide: false,
                navSearchHide: true,
                profHide: true,
                errHide: false,
                errCode: "userNotFound " + this.state.username,
                searchHide: false,
            });
        }
    }

  render() {

      return (
        <div className="body">
            <Navbar hidden={this.state.navHide} searchHidden={this.state.navSearchHide} />
            <Error hidden={this.state.errHide} errorCode={this.state.errCode} />
            <Search hidden={this.state.searchHide}/>
            <Profile hidden={this.state.profHide} userData={this.state.userData} repoData={this.state.userRepos} />

        </div>
    );
  }
}

App.defaultProps = {
    version: "1.0.0.0",
    clientId: "867e90a6447e459c4e64",
    clientSecret: "3d4b3708334612925445200148c18cd455cb0763",
    accessToken: "cfde940eafe7c40ef7e907db0986e0c36ac53f47"
};

export default App;



