import React, {Component} from 'react';

class Data extends Component {
    render() {

        let userLocation = this.props.userData.location, accountType = this.props.userData.type;

        if(this.props.userData.location === null)
            userLocation = "This user has not entered their location.";

        let creationDate = new Date(this.props.userData.created_at);
        let now = new Date();

        let difference = new Date(now - creationDate);
        let year = difference.getFullYear()-1970 === 1 ? "Year" : "Years";

        if(!(this.props.userData.plan === undefined))
            accountType = this.props.userData.plan.name.substring(0,1).toUpperCase() + this.props.userData.plan.name.substring(1);

        let spaceSizes = ["KB", "MB", "GB", "TB", "PB", "EB"];
        let currentSize = 0;
        let space = parseInt(this.props.userData.disk_usage, 10);
        if(this.props.userData.disk_usage === undefined)
            space = 0;
        while(space > 1024){
            space /= 1024;
            currentSize++;
        }

        space = space.toFixed(2);



        return (
            <div className="card dark text-center border-success" style={{width: "95%", margin: "auto", marginTop: "15px"}}>
                <h5 className="card-header font-weight-bold bg-success border-success">
                    User Specifics
                </h5>

                <div className="card-body row">

                        <div className="col-md-half" />

                        <div className="col-md-2" >
                            <div className="card dark border-warning" style={{marginBottom: "15px"}}>
                                <div className="card-header bg-warning font-weight-bold">
                                    User Location
                                </div>

                                <div className="card-body text-white">
                                    {userLocation}
                                </div>
                            </div>
                        </div>

                        <div className="col-md-1" />

                        <div className="col-md-2" >
                            <div className="card border-danger dark" style={{marginBottom: "15px"}}>
                                <div className="card-header bg-danger border-danger font-weight-bold">
                                    Account Age
                                </div>

                                <div className="card-body text-white">
                                    {difference.getFullYear() - 1970} {year}, {difference.getMonth()} Months, and {difference.getDate()} Days
                                </div>
                            </div>
                        </div>

                        <div className="col-md-1" />

                        <div className="col-md-2" >

                            <div className="card dark border-primary" style={{marginBottom: "15px"}}>
                                <div className="card-header bg-primary border-primary font-weight-bold">
                                    Account Type
                                </div>

                                <div className="card-body text-white">
                                    {accountType}
                                </div>
                            </div>
                        </div>

                        <div className="col-md-1" />

                        <div className="col-md-2" >

                            <div className="card border-info dark" style={{marginBottom: "15px"}}>
                                <div className="card-header bg-info border-info font-weight-bold">
                                    Disk Space Used
                                </div>

                                <div className="card-body text-white">
                                    {space + " " + spaceSizes[currentSize]}
                                </div>
                            </div>
                        </div>

                        <div className="col-md-half" />

                    </div>

                </div>
        );
    }
}

export default Data;


