import React, {Component} from 'react';


class Repo extends Component {
    render() {

        //console.log(this.props.repo);
        if(this.props.repo === undefined){
            return (
                <div className="card dark brd-dark txt-dark">NO-UNDEF</div>
            );
        }
        let $name, $description, $stars, $watchers, $issues, $forks, $language, $url, $time;

        $name = this.props.repo.name;
        $description = this.props.repo.description;
        $stars = this.props.repo.stargazers_count;
        $watchers = this.props.repo.watchers_count;
        $issues = this.props.repo.open_issues;
        $forks = this.props.repo.forks;
        $language = this.props.repo.language;
        $url = this.props.repo.html_url;

        let commit = new Date(this.props.repo.pushed_at);
        let now = new Date();

        let timeSinceLastCommit = new Date(now - commit);
        let $time_year = timeSinceLastCommit.getFullYear()-1970;
        let $time_month = timeSinceLastCommit.getMonth();
        let $time_day = timeSinceLastCommit.getDate();
        let $time_hour = timeSinceLastCommit.getHours();
        let $time_minute = timeSinceLastCommit.getMinutes();
        let $time_second = timeSinceLastCommit.getSeconds();
        let year = $time_year === 1 ? " year" : " years";
        let month = $time_month === 1 ? " month" : " months";
        let day = $time_day === 1 ? " day" : " days";
        let hour = $time_hour === 1 ? " hour" : " hours";
        let minute = $time_minute === 1 ? " minute" : " minutes";
        let second = $time_second === 1 ? " second" : " seconds";


        if($time_year > 0 && ($time_month > 0 || $time_day > 0 || $time_hour > 0 || $time_minute > 0 || $time_second > 0))
            $time = "more than " + $time_year + year;
        else{
            $time = $time_year + year;

            if($time_month > 0 && ($time_day > 0 || $time_hour > 0 || $time_minute > 0 || $time_second > 0))
                $time = "more than " + $time_month + month;
            else{
                $time = $time_month + month;

                if($time_day > 0)
                    $time = $time_day + day;
                else{
                    if($time_hour > 0)
                        $time = $time_hour + hour;
                    else{
                        if($time_minute > 0)
                            $time = $time_minute + minute;
                        else{
                            $time = $time_second + second;
                        }
                    }
                }
            }

        }

        return (
            <div className="card dark border-primary">
                <h6 className="card-header text-center border-primary text-white font-weight-bold ">{$name}</h6>
                <div className="card-body text-center" style={{position: "relative", paddingBottom: "3rem", paddingTop: ".5rem"}}>
                    <div className="language text-danger font-weight-bold text-center" style={{width: "100%",marginBottom: ".5rem"}}>
                        <i className="icon-code" />
                        {$language}
                    </div>
                    <div className="row">
                        <div className="col-md-7 text-center">
                            <div className="description text-white" style={{marginBottom: ".5rem"}}>
                               {$description}
                            </div>

                        </div>

                        <div className="col-md-5">
                            <div className="card bg-warning mb-3" style={{height: "1.25rem"}}>
                                <h6 className="card-text" >
                                    <i className="icon-star" />|
                                    {$stars}
                                </h6>
                            </div>
                            <div className="card bg-danger mb-3" style={{height: "1.25rem"}}>
                                <h6 className="card-text" >
                                    <i className="icon-eye-1" />|
                                    {$watchers}
                                </h6>
                            </div>
                            <div className="card bg-info mb-3" style={{height: "1.25rem"}}>
                                <h6 className="card-text" >
                                    <i className="icon-exclamation" />|
                                    {$issues}
                                </h6>
                            </div>
                            <div className="card bg-success mb-3" style={{height: "1.25rem"}}>
                                <h6 className="card-text" >
                                    <i className="icon-fork" />|
                                    {$forks}
                                </h6>
                            </div>
                        </div>
                    </div>

                    <a href={$url} >
                        <button className="btn btn-primary m-sm-auto" style={{position: "absolute", bottom: ".5rem", left: "32.5%", right: "32.5%", width: "35%"}}>View on Github </button>
                    </a>
                </div>

                <div className="card-footer border-primary text-white text-center">Last commit {$time} ago</div>
            </div>
        );
    }
}

export default Repo;


