import React, {Component} from 'react';

class Search extends Component {
    render() {

        return (
            <div hidden = {this.props.hidden} style={{marginTop: "1rem"}}>
                <div className="card bg-info text-center align-content-sm-center" style={{width: "25rem", margin: "auto"}}>
                    <div className="card-header font-weight-bold ">
                        Search
                    </div>

                    <div className="card-body">
                        <p className="card-text">Search for a Github username to begin!</p>
                        <form className="form-inline my-2 my-lg-0 align-self-center" style={{width: "20rem", margin:"auto"}} id="search-search">
                            <input className="form-control mr-sm-2 bg-dark btn-outline-info text-white" style={{width: "15rem"}} type="text" placeholder="Enter Github Username" id="username_search"/>
                            <button className="btn btn-success my-2 my-sm-0" style={{marginLeft: "1rem", flex: "grow"}}>Go!</button>
                        </form>
                    </div>
                </div>
            </div>
        );
    }
}

export default Search;


